import React from 'react';
import s from './Messages.module.css';


const Chat = (props) => {
    return (
        <div className={s.chat}>
            <div className={s.heading}>Name User</div>
            <div className={s.main_chat}>

                <div className={s.avatar_user}>
                    <img src="https://pp.userapi.com/c834204/v834204558/15de25/g0kQP8tAO1I.jpg?ava=1"/>
                </div>
                <div className={s.name_message}>

                    <div className={s.name_user}>
                        <span>name_of_the_person_who_wrote_the_message</span>
                    </div>
                    <div className={s.message}>
                        <span>
                            message
                        </span>
                    </div>
                </div>
            </div>

            <div className={s.messag_field}>
                <div className={s.write_message}>
                    <textarea></textarea>
                </div>
                <div className={s.btn_post_reply}>
                    <button>Post Reply</button>
                </div>
            </div>
        </div>
    )
};
export default Chat;