import React from 'react';
import s from './Dialog.module.css';
import {NavLink} from "react-router-dom";


const Dialog = (props) => {
    let path = "/dialogs/" + props.dialogUser.id;
    return (
        <div className={s.dialog}>
            <div>
                <img src={props.dialogUser.avatar}/>
            </div>
            <div className={s.nameUser}>
                <NavLink to={path}>{props.dialogUser.name}</NavLink>
                <span>
                   last message
                </span>
                <span className={s.time}>
                    4 hours ago
                </span>
            </div>
        </div>
    );
};


export default Dialog;